body {
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.watch {
  border: 5px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.watch__center {
  z-index: 3;
  background-color: orange;
  border-radius: 50%;
  align-self: center;
  width: 38px;
  height: 38px;
  margin: 0 auto;
  display: flex;
}

.watch__hour, .watch__minute {
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
}

.watch__hour {
  animation-name: time;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform: rotate(-90deg);
}

.watch__minute {
  animation-name: time;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform: rotate(180deg);
}

.watch__hour:before {
  content: "";
  background-color: #00f;
  width: 15px;
  height: 25vmin;
  position: absolute;
}

.watch__minute:before {
  content: "";
  background-color: green;
  width: 10px;
  height: 40vmin;
  position: absolute;
}

@keyframes time {
  from {
    transform: rotate(180deg);
  }

  to {
    transform: rotate(540deg);
  }
}

